.Aline{
    text-align: left;
}


.master-image{
    width: 100%;
}

.container{
    width: 70%;
    height: 70%;
    margin: 100px auto;
}


.black-bg{
    background-color: #2e2e2e;
    padding: 60px;
}

.root {
    display: flex;
    offset: 60;
    max-width: 1440;
    grid-template-columns: 1fr;
}

.text-title{
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;
}



.text-explanation{
    font-style: normal;
    font-weight: normal;
    font-size: 17px;
    line-height: 150%;
}

.text-body{
    font-style: normal;
    font-weight: normal;
    font-size: 17px;
    line-height: 150%;
    margin-top: 20px;
}

.text-subtitle{
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 21px;
}

.text-subtitle-white{
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 21px;
    color : #FFFFFF;
}

a {
    color:black;
  }

.player-wrapper {
    position: relative;
    padding-top: 56.25%; /* Player ratio: 100 / (1280 / 720) */
    width: 70%;
    margin: 100px auto;
}
  
.react-player {
    position: absolute;
    top: 0;
    left: 0;
  }